import React, { useState, useEffect } from "react";
import { useCookies } from "react-cookie";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { url } from "../const";
import { Header } from "../components/Header";
import "./newTask.scss";

export function NewTask() {
  const [selectListId, setSelectListId] = useState();
  const [lists, setLists] = useState([]);
  const [title, setTitle] = useState("");
  const [detail, setDetail] = useState("");
  const [limit, setLimit] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [cookies] = useCookies();
  const Navigate = useNavigate();
  const handleTitleChange = (e) => setTitle(e.target.value);
  const handleDetailChange = (e) => setDetail(e.target.value);
  const handleSelectList = (id) => setSelectListId(id);
  const handleLimitChange = (e) => {
    const date = new Date(e.target.value);
    const formattedDate = date.toISOString();
    setLimit(formattedDate);
  };

  const onCreateTask = () => {
    const data = {
      title,
      detail,
      limit,
      done: false,
    };

    axios
      .post(`${url}/lists/${selectListId}/tasks`, data, {
        headers: {
          authorization: `Bearer ${cookies.token}`,
        },
      })
      .then(() => {
        Navigate("/");
      })
      .catch((err) => {
        setErrorMessage(`タスクの作成に失敗しました。${err}`);
      });
  };

  useEffect(() => {
    axios
      .get(`${url}/lists`, {
        headers: {
          authorization: `Bearer ${cookies.token}`,
        },
      })
      .then((res) => {
        setLists(res.data);
        setSelectListId(res.data[0]?.id);
        setLimit(res.data);
      })
      .catch((err) => {
        setErrorMessage(`リストの取得に失敗しました。${err}`);
      });
  }, [cookies]);

  useEffect(() => {
    const limitSet = document.getElementById("limit");
    if (limitSet) {
      limitSet.addEventListener("change", (event) => {
        const selectDate = new Date(event.target.value);
        console.log(selectDate);
      });
    }
  }, []);

  return (
    <div>
      <Header />
      <main className="new-task">
        <h2>タスク新規作成</h2>
        <p className="error-message">{errorMessage}</p>
        <form className="new-task-form">
          <label htmlFor="newtask-list">
            リスト
            <br />
            <select
              onChange={(e) => handleSelectList(e.target.value)}
              className="new-task-select-list"
            >
              {lists.map((list, key) => (
                <option key={key} className="list-item" value={list.id}>
                  {list.title}
                </option>
              ))}
            </select>
            <br />
          </label>
          <label htmlFor="new-task-title">
            タイトル
            <br />
            <input
              type="text"
              onChange={handleTitleChange}
              className="new-task-title"
              required
            />
            <br />
          </label>
          <label htmlFor="new-task-detail">
            詳細
            <br />
            <textarea
              type="text"
              onChange={handleDetailChange}
              className="new-task-detail"
            />
            <br />
          </label>
          <label htmlFor="new-task-limit">
            期限
            <br />
            <input
              type="datetime-local"
              onChange={handleLimitChange}
              className="new-task-limit"
            />
          </label>
          <br />
          <br />
          <button
            type="button"
            className="new-task-button"
            onClick={onCreateTask}
          >
            作成
          </button>
        </form>
      </main>
    </div>
  );
}

export default NewTask;
