import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useCookies } from "react-cookie";
import axios from "axios";
import PropTypes from "prop-types";
import { Header } from "../components/Header";
import { url } from "../const";
import "./home.scss";

export function Home() {
  const [isDoneDisplay, setIsDoneDisplay] = useState("todo"); // todo->未完了 done->完了
  const [lists, setLists] = useState([]);
  const [selectListId, setSelectListId] = useState("");
  const [tasks, setTasks] = useState([]);
  const [errorMessage, setErrorMessage] = useState("");
  const [cookies] = useCookies();
  const handleIsDoneDisplayChange = (e) => setIsDoneDisplay(e.target.value);
  useEffect(() => {
    axios
      .get(`${url}/lists`, {
        headers: {
          authorization: `Bearer ${cookies.token}`,
        },
      })
      .then((res) => {
        setLists(res.data);
      })
      .catch((err) => {
        setErrorMessage(`リストの取得に失敗しました。${err}`);
      });
  }, []);

  useEffect(() => {
    const listId = lists[0]?.id;
    if (typeof listId !== "undefined") {
      setSelectListId(listId);
      axios
        .get(`${url}/lists/${listId}/tasks`, {
          headers: {
            authorization: `Bearer ${cookies.token}`,
          },
        })
        .then((res) => {
          setTasks(res.data.tasks);
        })
        .catch((err) => {
          setErrorMessage(`タスクの取得に失敗しました。${err}`);
        });
    }
  }, [lists]);

  useEffect(() => {
    if (selectListId) {
      axios
        .get(`${url}/lists/${selectListId}/tasks`, {
          headers: {
            authorization: `Bearer ${cookies.token}`,
          },
        })
        .then((res) => {
          setTasks(res.data.tasks);
        })
        .catch((err) => {
          setErrorMessage(`タスクの取得に失敗しました。${err}`);
        });
    }
  }, [selectListId]);
  const handleSelectList = (id) => {
    setSelectListId(id);
  };
  const handleKeyDown = (e) => {
    if (e.key === "Tab" || e.keyCode === 9) {
      e.preventDefault();
      const nextIndex = lists.findIndex((list) => list.id === selectListId) + 1;
      const nextList = lists[nextIndex % lists.length];
      handleSelectList(nextList.id);
    }
  };

  return (
    <div>
      <Header />
      <main className="taskList">
        <p className="error-message">{errorMessage}</p>
        <div>
          <div className="list-header">
            <h2>リスト一覧</h2>
            <div className="list-menu">
              <p>
                <Link to="/list/new">リスト新規作成</Link>
              </p>
              <p>
                <Link to={`/lists/${selectListId}/edit`}>
                  選択中のリストを編集
                </Link>
              </p>
            </div>
          </div>
          <div className="list-flex">
            <ul className="list-tab">
              {lists.map((list, key) => {
                const isActive = list.id === selectListId;
                return (
                  <li
                    key={key}
                    className={`list-tab-item ${isActive ? "active" : ""}`}
                    onClick={() => handleSelectList(list.id)}
                    onFocus={() => handleSelectList(list.id)}
                    role="tab"
                    aria-label="list"
                    aria-selected={isActive}
                    tabIndex={isActive ? 0 : 1}
                    onKeyDown={handleKeyDown}
                  >
                    {list.title}
                  </li>
                );
              })}
            </ul>
          </div>
          <div className="tasks">
            <div className="tasks-header">
              <h2>タスク一覧</h2>
              <Link to="/task/new">タスク新規作成</Link>
            </div>
            <div className="display-select-wrapper">
              <select
                onChange={handleIsDoneDisplayChange}
                className="display-select"
              >
                <option value="todo">未完了</option>
                <option value="done">完了</option>
              </select>
            </div>
            <Tasks
              tasks={tasks}
              selectListId={selectListId}
              isDoneDisplay={isDoneDisplay}
            />
          </div>
        </div>
      </main>
    </div>
  );
}

// 表示するタスク
function Tasks(props) {
  const { tasks, selectListId, isDoneDisplay } = props;
  Tasks.propTypes = {
    tasks: PropTypes.array.isRequired,
    selectListId: PropTypes.string.isRequired,
    isDoneDisplay: PropTypes.string.isRequired,
  };
  const getRemainingTime = (limit) => {
    const now = new Date();
    const limitDate = new Date(limit);
    const diff = limitDate - now;
    const diffDays = Math.floor(diff / (1000 * 60 * 60 * 24));
    const diffHours = Math.floor(
      (diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
    );
    const diffMinutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
    if (diff < 0) {
      return ``;
    } else {
      return `残り${diffDays} 日 ${diffHours} 時間 ${diffMinutes} 分`;
    }
  };
  if (tasks === null) return <></>;

  if (isDoneDisplay == "done") {
    return (
      <ul>
        {tasks
          .filter((task) => task.done === true)
          .map((task, key) => (
            <li key={key} className="task-item">
              <Link
                to={`/lists/${selectListId}/tasks/${task.id}`}
                className="task-item-link"
              >
                {task.title}
                <br />
                {task.done
                  ? `${new Date(task.limit).toLocaleString()} 完了`
                  : `${new Date(task.limit).toLocaleString()} 未完了`}
                <br />
              </Link>
            </li>
          ))}
      </ul>
    );
  }

  return (
    <ul>
      {tasks
        .filter((task) => task.done === false)
        .map((task, key) => (
          <li key={key} className="task-item">
            <Link
              to={`/lists/${selectListId}/tasks/${task.id}`}
              className="task-item-link"
            >
              {task.title}
              <br />
              {task.done
                ? `${new Date(task.limit).toLocaleString()} 完了`
                : `${new Date(task.limit).toLocaleString()} 未完了`}
              <br />
              {getRemainingTime(task.limit)}
            </Link>
          </li>
        ))}
    </ul>
  );
}
export default Home;
